.block {
  display: inline-block;
  width: 50%;
  border: 5px solid #e7e7e7;
  background-color: #555555;
  color: white;
  padding: 14px 28px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  margin: 0 0 20px 0;
  border-radius: 8px;
}
.checked {
  background-color: #4CAF50;
  cursor: not-allowed;
}
.block:hover {
  background-color: #ddd;
  color: black;
}

